// Wrapper class to control general paddings

.wrapper{
    padding-right: 15px;
    padding-left: 15px;

    @media all and (min-width: 768px) {
        padding-right: 39px;
        padding-left: 39px;
    }

    @media all and (min-width: 1280px) {
        padding-right: 275px;
        padding-left: 275px;
    }
}

