.main-header{
    min-height: 100vh;
  
    // Multiple backgrounds: a gradient + a hero image
    background: 
    linear-gradient(rgba(white, 0.1), (rgba(black, 0.2))),
    url("../images/cover.jpg") center center no-repeat;
    background-size: cover;

    // Perform to achieve a more fluid layout and perform well in Portrait mobile :
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    padding-bottom: $button-height;
    
    @media all and (min-width: 1280px) {
        padding-bottom: $button-height/2;
    }
}

.main-title{
    font-family: $font-secundary;
}

.main-subtitle{
    font-family: $font-primary;
    margin: 10px auto 45px;

    @media all and (min-width: 768px) {
        margin: 20px auto 30px;
    }

    @media all and (min-width: 1280px) {
        margin: 10px auto 30px;
    }
}